
.container {
  width: 100%;
  max-width: 500px;
}
h1 {
  margin: 0;
}
td.result {
  width: 23%;
}
td.result img {
  width: 100%;
}
td.info{
  text-align: left;
  font-weight: bold;
}
main {
  position: relative;
  width: 100%;
  padding-bottom: 100%;
  margin: 10px auto;
}
p.block {
  position: absolute;
  margin: 0;
  background-repeat: no-repeat;
  display: inline-block;
  transition: all .2s;
}
.controls {
  width: 100%;
}
.controls button {
  font-size: 20px;
  margin: 2px 10px;
}
input[type='file'] {
  display: none;
}



